<template>
  <div class="card">
    <div class="card-image">
      <!--<figure class="image is-4by3">
        <img v-bind:src="image">
      </figure>-->
    </div>
    <div class="content">
      <router-link v-bind:to="route">{{title}}</router-link>
      <p>{{author}} - {{date}}</p>
    </div>
  </div>
</template>

<script>
export default {
  name: 'ArticleCard',

  props: ["title", "image", "author", "date", "link", "route"],

  data () {
    return {
      
    };
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss">
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
</style>



